@import url(//fonts.googleapis.com/css?family=Press+Start+2P);

.container {
    height: 300vh;
    width: 100%;
}

.container-1 {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 100vh;
    width: 100%;
    background: linear-gradient(0deg, rgb(33, 32, 39) 0%, rgb(69, 69, 77) 35%, rgb(191, 196, 197) 100%);
}

.container-1-banner {
    width: 100%;
    height: 60%;
}

.container-1-title {
    font-size: 6rem;
    color: white;
    font-family: 'Press Start 2P';
    font-style: italic;
    margin-top: -35rem;
}


.container-1-social {
    width: 50%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}

.container-1-social img {
    width: 3rem;
}

.container-1-social img:hover {
    transform: scale(1.2);
    filter: invert(40%) sepia(0%) saturate(348%) hue-rotate(244deg) brightness(87%) contrast(90%);
}

.container-2 {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;  
    padding: 5rem 0rem;
    background: linear-gradient(0deg, rgb(33, 32, 39) 0%, rgb(69, 69, 77) 35%, rgb(191, 196, 197) 100%);
}

.container-3 {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    background: linear-gradient(0deg, rgb(33, 32, 39) 0%, rgb(69, 69, 77) 35%, rgb(191, 196, 197) 100%);
}

.container-3-team {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.section-title {
    font-family: 'Press Start 2P';
    font-size: 4rem;
    font-style: italic;
    color: white;
    margin-bottom: 6rem;
}
